@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700;900&display=swap');

/* RESET */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* BASE */
body {
  font-family: 'Poppins', sans-serif;
  background: #ffffff;
  color: #0a0a0a;
  overflow-x: hidden;
  transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  line-height: 1.6;
}

/* ACCESSIBILITY */
button:focus,
a:focus,
input:focus,
textarea:focus {
  outline: 3px solid #6B8E23;
  outline-offset: 3px;
}

/* APP WRAPPER */
.app {
  padding-top: 20px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* LOADER */
.loader-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.dark-mode .loader-wrapper {
  background: #0a0a0a;
}

/* DARK MODE */
.dark-mode {
  background: #0a0a0a;
  color: #f8d2b7;
}

.dark-mode .navbar {
  background: rgba(10, 10, 10, 0.85);
}

.dark-mode .hero,
.dark-mode .projects,
.dark-mode .contact-hero {
  background: #0a0a0a;
}

.dark-mode .hero-description,
.dark-mode .contact-info p,
.dark-mode .project-card p,
.dark-mode .case-study p,
.dark-mode .case-study li,
.dark-mode .case-study .meta {
  color: #b8b8b8;
}

.dark-mode .project-card,
.dark-mode .contact-form,
.dark-mode .case-study .feature,
.dark-mode .case-study .highlight {
  background: #0f0f0f;
  border-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0 6px 15px rgba(107, 142, 35, 0.2);
}

.dark-mode .project-card:hover {
  box-shadow: 0 10px 25px rgba(107, 142, 35, 0.35);
}

.dark-mode .project-card h3,
.dark-mode .case-study h3,
.dark-mode .case-study .highlight,
.dark-mode .contact-info h1 {
  color: #ffffff;
}

.dark-mode .form-group label {
  color: #ffffff;
}

.dark-mode .form-group input,
.dark-mode .form-group textarea {
  background: #1a1a1a;
  border-color: rgba(255, 255, 255, 0.1);
  color: #ffffff;
}

.dark-mode footer {
  background: #0a0a0a;
  border-top-color: rgba(255, 255, 255, 0.1);
}

.dark-mode .logo,
.dark-mode .nav-link,
.dark-mode .hamburger {
  color: #fff;
}

.dark-mode .logo img {
  filter: brightness(3);
}

/* NAVBAR */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 1.2rem 5%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1000;
  background: rgba(255, 255, 255, 0.85);
  backdrop-filter: blur(20px);
}

/* LOGO */
.logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: none;
  border: none;
  cursor: pointer;
  color: inherit;
  transition: transform 0.3s ease;
}

.logo:hover {
  transform: scale(1.05);
}

.logo img {
  width: 40px;
  height: 40px;
}

.logo-text {
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: 2px;
}

/* HAMBURGER MENU */
.hamburger {
  display: none;
  background: none;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  color: inherit;
  transition: transform 0.3s ease;
}

.hamburger:hover {
  transform: scale(1.1);
}

/* NAV MENU */
.nav-menu {
  display: flex;
  gap: 3rem;
  list-style: none;
  align-items: center;
}

.nav-menu li {
  margin: 0;
}

.nav-link,
.theme-btn {
  background: none;
  border: none;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 0.85rem;
  cursor: pointer;
  position: relative;
  color: inherit;
  text-decoration: none;
  padding: 1rem 0;
  transition: color 0.25s ease;
}

.theme-btn {
  font-size: 1.5rem;
  padding: 0.5rem;
}

.theme-btn:hover {
  transform: scale(1.2);
}

/* UNDERLINE ON HOVER */
.nav-link::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background: #6B8E23;
  transition: width 0.25s ease;
}

.nav-link:hover::after {
  width: 100%;
}

/* HERO SECTION */
.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 80px 5% 40px;
  text-align: center;
  background: radial-gradient(circle at 20% 50%, rgba(128, 128, 0, 0.08), transparent 50%),
              radial-gradient(circle at 80% 80%, rgba(107, 142, 35, 0.08), transparent 50%),
              #ffffff;
}

.hero-content {
  max-width: 900px;
  margin: 0 auto;
}

.hero-title {
  font-size: 8rem;
  font-weight: 900;
  letter-spacing: -5px;
  line-height: 1;
  background: linear-gradient(135deg, #808000 0%, #6B8E23 50%, #556B2F 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 2rem;
}

.hero-description {
  font-size: 1.3rem;
  margin: 2rem auto 3rem;
  color: #666;
  line-height: 1.8;
  max-width: 700px;
}

.hero-btn {
  padding: 1.2rem 3rem;
  background: linear-gradient(135deg, #808000 0%, #6B8E23 100%);
  color: white;
  border: none;
  border-radius: 50px;
  font-weight: 600;
  font-size: 1rem;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.hero-btn:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(107, 142, 35, 0.4);
}

/* SECTIONS */
.section {
  display: none;
}

.section.active {
  display: block;
}

/* PROJECTS SECTION */
.projects {
  min-height: 100vh;
  padding: 100px 5% 60px;
  background: #ffffff;
}

.section-title {
  font-size: 4rem;
  font-weight: 900;
  text-align: center;
  margin-bottom: 4rem;
  letter-spacing: 1px;
  background: linear-gradient(135deg, #808000 0%, #6B8E23 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 3rem;
  max-width: 1400px;
  margin: 0 auto;
}

.project-card {
  background: #f8f8f8;
  border-radius: 30px;
  padding: 3rem;
  border: 1px solid #e0e0e0;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  box-shadow: 0 6px 15px rgba(107, 142, 35, 0.1);
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 35px rgba(107, 142, 35, 0.25);
}

.project-card h3 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  color: #0a0a0a;
  font-weight: 700;
}

.project-card p {
  color: #666;
  line-height: 1.8;
  font-size: 1rem;
}

/* CONTACT SECTION */
.contact-hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 100px 5% 60px;
  background: radial-gradient(circle at 20% 50%, rgba(128, 128, 0, 0.08), transparent 50%),
              radial-gradient(circle at 80% 80%, rgba(107, 142, 35, 0.08), transparent 50%),
              #ffffff;
}

.contact-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5rem;
  max-width: 1200px;
  width: 100%;
  align-items: center;
}

.contact-info h1 {
  font-size: 5rem;
  font-weight: 900;
  background: linear-gradient(135deg, #808000 0%, #6B8E23 50%, #556B2F 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 2rem;
  line-height: 1.1;
}

.contact-info p {
  font-size: 1.3rem;
  color: #666;
  line-height: 1.8;
}

.contact-form {
  background: #f8f8f8;
  padding: 3rem;
  border-radius: 30px;
  border: 1px solid #e0e0e0;
}

.form-group {
  margin-bottom: 2rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.8rem;
  font-weight: 600;
  color: #0a0a0a;
  font-size: 0.95rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 1.2rem;
  border: 2px solid #e0e0e0;
  border-radius: 15px;
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  transition: border-color 0.3s;
  background: white;
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: #6B8E23;
}

.form-group textarea {
  resize: vertical;
  min-height: 150px;
}

.submit-btn {
  width: 100%;
  padding: 1.3rem;
  background: linear-gradient(135deg, #808000 0%, #6B8E23 100%);
  color: white;
  border: none;
  border-radius: 50px;
  font-weight: 600;
  font-size: 1.1rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.submit-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 30px rgba(107, 142, 35, 0.4);
}

/* CASE STUDY PAGES */
.case-study {
  max-width: 900px;
  margin: 0 auto;
  padding: 120px 5% 60px;
  line-height: 1.8;
}

.case-study h1 {
  font-size: 3.5rem;
  font-weight: 900;
  background: linear-gradient(135deg, #808000 0%, #6B8E23 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 1rem;
  margin-top: 0;
}

.case-study .meta {
  color: #666;
  font-size: 1.1rem;
  margin-bottom: 3rem;
  font-style: italic;
}

.case-study section {
  margin-bottom: 3rem;
}

.case-study h2 {
  font-size: 2.2rem;
  color: #6B8E23;
  margin: 3rem 0 1.5rem;
  font-weight: 700;
}

.case-study h3 {
  font-size: 1.6rem;
  color: #0a0a0a;
  margin: 2rem 0 1rem;
  font-weight: 600;
}

.case-study p {
  font-size: 1.1rem;
  color: #666;
  margin-bottom: 1.5rem;
  line-height: 1.9;
}

.case-study .highlight {
  background: #f8f8f8;
  padding: 2rem;
  border-left: 4px solid #6B8E23;
  border-radius: 10px;
  font-size: 1.2rem;
  font-weight: 600;
  color: #0a0a0a;
  margin: 2rem 0;
}

.case-study ul {
  margin-left: 2rem;
  margin-bottom: 1.5rem;
}

.case-study li {
  font-size: 1.1rem;
  color: #666;
  margin-bottom: 0.8rem;
  line-height: 1.7;
}

.case-study strong {
  color: #0a0a0a;
  font-weight: 600;
}

.case-study .feature {
  background: #f8f8f8;
  padding: 2rem;
  border-radius: 20px;
  margin-bottom: 1.5rem;
  border: 1px solid #e0e0e0;
}

.case-study .feature h3 {
  margin-top: 0;
  color: #6B8E23;
}

/* FOOTER */
footer {
  background: #ffffff;
  padding: 3rem 5%;
  text-align: center;
  border-top: 1px solid #e0e0e0;
  margin-top: auto;
}

footer p {
  color: #666;
  font-size: 0.9rem;
  font-weight: 600;
}

/* ANIMATIONS */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* RESPONSIVE - TABLET */
@media (max-width: 968px) {
  .hamburger {
    display: block;
  }

  .nav-menu {
    position: fixed;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    background: rgba(255, 255, 255, 0.98);
    backdrop-filter: blur(20px);
    transition: right 0.4s ease;
    gap: 2rem;
  }

  .nav-menu.open {
    right: 0;
  }

  .dark-mode .nav-menu {
    background: rgba(10, 10, 10, 0.98);
  }

  .hero-title {
    font-size: 4rem;
    letter-spacing: -3px;
  }

  .contact-info h1,
  .case-study h1 {
    font-size: 3rem;
  }

  .section-title {
    font-size: 2.5rem;
  }

  .projects-grid {
    grid-template-columns: 1fr;
  }

  .contact-container {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .contact-form {
    padding: 2rem;
  }
}

/* RESPONSIVE - MOBILE */
@media (max-width: 600px) {
  .navbar {
    padding: 1rem 5%;
  }

  .logo img {
    width: 35px;
    height: 35px;
  }

  .logo-text {
    font-size: 1rem;
  }

  .hero {
    padding: 100px 5% 40px;
  }

  .hero-title {
    font-size: 3rem;
  }

  .hero-description {
    font-size: 1.1rem;
  }

  .hero-btn {
    padding: 1rem 2rem;
    font-size: 0.9rem;
  }

  .section-title {
    font-size: 2rem;
  }

  .projects {
    padding: 80px 5% 40px;
  }

  .projects-grid {
    gap: 2rem;
  }

  .project-card {
    padding: 2rem;
  }

  .project-card h3 {
    font-size: 1.5rem;
  }

  .contact-hero {
    padding: 80px 5% 40px;
  }

  .contact-info h1 {
    font-size: 2.5rem;
  }

  .contact-info p {
    font-size: 1.1rem;
  }

  .case-study {
    padding: 100px 5% 40px;
  }

  .case-study h1 {
    font-size: 2.5rem;
  }

  .case-study h2 {
    font-size: 1.8rem;
  }

  .case-study h3 {
    font-size: 1.4rem;
  }

  .case-study p,
  .case-study li {
    font-size: 1rem;
  }
}

/* EXTRA SMALL PHONES */
@media (max-width: 480px) {
  .logo-text {
    display: none;
  }

  .hero-title {
    font-size: 2.5rem;
    letter-spacing: -2px;
  }
}


/* =========================
   CASE STUDY (Good Harvest)
   ========================= */
   /* -----------------------------
   PROJECT CARDS: match sizing + add media header
-------------------------------- */
.projects {
  padding: 8rem 5%;
  background: #ffffff;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
  max-width: 1100px; /* keeps grid centered + consistent */
  margin: 0 auto;
}

.project-card {
  background: #fff;
  border-radius: 26px;
  border: 1px solid rgba(0,0,0,0.10);
  padding: 0; /* because header will handle top */
  overflow: hidden;
  cursor: pointer;
  box-shadow: 0 8px 22px rgba(0, 0, 0, 0.08);
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.project-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 14px 34px rgba(0,0,0,0.14);
}

.dark-mode .project-card {
  background: #36220c;
  border-color: rgba(248, 210, 183, 0.18);
  box-shadow: 0 10px 28px rgba(0,0,0,0.35);
}

/* New media header (icon or image) */
.project-media {
  height: 170px;
  display: grid;
  place-items: center;
  background: linear-gradient(135deg, rgba(235,163,82,0.25), rgba(186,128,63,0.20));
}

.dark-mode .project-media {
  background: linear-gradient(135deg, rgba(235,163,82,0.20), rgba(26,14,3,0.0));
}

.project-media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.project-icon {
  font-size: 2.25rem;
  width: 58px;
  height: 58px;
  border-radius: 16px;
  display: grid;
  place-items: center;
  background: rgba(255,255,255,0.65);
  border: 1px solid rgba(0,0,0,0.10);
}

.dark-mode .project-icon {
  background: rgba(54,34,12,0.85);
  border-color: rgba(248, 210, 183, 0.22);
}

.project-body {
  padding: 1.4rem 1.5rem 1.6rem;
}

.project-body h3 {
  font-size: 1.2rem;
  letter-spacing: 0.4px;
  margin-bottom: 0.4rem;
}

.project-body p {
  color: #444;
  line-height: 1.55;
  font-size: 0.98rem;
  display: -webkit-box;
  -webkit-line-clamp: 3;  /* clamps long blurbs */
  line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.dark-mode .project-body p {
  color: #f8d2b7;
}

/* -----------------------------
   CASE STUDY MEDIA: match projects grid width
-------------------------------- */
.cs-gallery {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 3rem; /* more breathing room */
  margin: 3rem 0;
}

@media (max-width: 1024px) {
  .cs-gallery {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .cs-gallery {
    grid-template-columns: 1fr;
  }
}


/* 3-column layout */
.cs-gallery.cols-3 {
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.media-card {
  background: var(--card-bg, #fff);
  border-radius: 18px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  box-shadow: 0 6px 18px rgba(0,0,0,0.08);
}

/* Image behavior */
.media-card img {
  width: 100%;
  height: auto;
  max-height: 280px; /* keeps them compact */
  object-fit: contain; /* preserves detail */
}



.cs-media figcaption {
  font-size: 0.9rem;
  line-height: 1.4;
  color: #555;
}

.dark-mode .cs-media figcaption {
  color: #e8d5c4;
}

.cs-media {
  display: flex;
  flex-direction: column;
}

.cs-actions {
  margin-top: auto;
}

.cs-link {
  font-size: 0.85rem;
  font-weight: 600;
  text-decoration: none;
  color: #6B8E23;
}

.cs-link:hover {
  text-decoration: underline;
}

/* =========================
   CASE STUDY: Recruiter skim card + compact media grid
   ========================= */

/* Recruiter quick-skim card */
.cs-skim {
  margin: 2.25rem 0 3rem;
  padding: 1.5rem;
  border-radius: 22px;
  border: 1px solid rgba(0,0,0,0.10);
  background:
    radial-gradient(circle at 20% 30%, rgba(128,128,0,0.08), transparent 55%),
    radial-gradient(circle at 85% 70%, rgba(107,142,35,0.08), transparent 55%),
    #ffffff;
  box-shadow: 0 10px 28px rgba(0,0,0,0.08);
}

.dark-mode .cs-skim {
  background:
    radial-gradient(circle at 20% 30%, rgba(235,163,82,0.16), transparent 60%),
    radial-gradient(circle at 85% 70%, rgba(186,128,63,0.14), transparent 60%),
    #0f0f0f;
  border-color: rgba(248, 210, 183, 0.18);
  box-shadow: 0 12px 30px rgba(0,0,0,0.38);
}

.cs-skim-head {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  gap: 1rem;
  margin-bottom: 1rem;
}

.cs-skim-title {
  font-size: 1rem;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  font-weight: 700;
  color: #0a0a0a;
}

.dark-mode .cs-skim-title {
  color: #ffffff;
}

.cs-skim-meta {
  font-size: 0.95rem;
  color: #666;
  font-style: italic;
}

.dark-mode .cs-skim-meta {
  color: #b8b8b8;
}

.cs-skim-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 1.25rem;
}

@media (max-width: 768px) {
  .cs-skim-grid {
    grid-template-columns: 1fr;
  }
}

.cs-skim-block h3 {
  font-size: 0.95rem;
  text-transform: uppercase;
  letter-spacing: 0.10em;
  margin-bottom: 0.5rem;
  color: #6B8E23;
  font-weight: 700;
}

.dark-mode .cs-skim-block h3 {
  color: #EBA352;
}

.cs-skim-block p {
  margin: 0;
  font-size: 1rem;
  line-height: 1.7;
  color: #444;
}

.dark-mode .cs-skim-block p {
  color: #e8d5c4;
}

/* Tags */
.cs-skim-tags {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem;
  padding: 0;
  margin: 0;
}

.cs-tag {
  font-size: 0.9rem;
  padding: 0.45rem 0.75rem;
  border-radius: 999px;
  background: rgba(107,142,35,0.10);
  border: 1px solid rgba(107,142,35,0.22);
  color: #0a0a0a;
  font-weight: 600;
}

.dark-mode .cs-tag {
  background: rgba(235,163,82,0.12);
  border-color: rgba(235,163,82,0.22);
  color: #ffffff;
}

/* =========================
   Case study media grid (smaller, card-like, 3 columns)
   ========================= */

/* Make the grid feel like your project cards */
.cs-gallery {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 1.5rem;              /* tighter than 3rem */
  margin: 2rem 0 3rem;
  align-items: start;
}

@media (max-width: 1024px) {
  .cs-gallery {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 768px) {
  .cs-gallery {
    grid-template-columns: 1fr;
  }
}

/* Card */
.media-card {
  background: #fff;
  border-radius: 18px;
  padding: 1rem;
  border: 1px solid rgba(0,0,0,0.10);
  box-shadow: 0 10px 26px rgba(0,0,0,0.08);
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.dark-mode .media-card {
  background: #0f0f0f;
  border-color: rgba(248, 210, 183, 0.18);
  box-shadow: 0 12px 30px rgba(0,0,0,0.38);
}

/* Clickable image area (fix “images aren’t linking”) */
.media-link {
  display: block;
  border-radius: 14px;
  overflow: hidden;
  border: 1px solid rgba(0,0,0,0.10);
  background: rgba(0,0,0,0.02);
}

.dark-mode .media-link {
  border-color: rgba(248, 210, 183, 0.14);
  background: rgba(255,255,255,0.03);
}

.media-link:focus {
  outline: 3px solid #6B8E23;
  outline-offset: 3px;
}

.dark-mode .media-link:focus {
  outline-color: #EBA352;
}

/* Compact image sizing, still readable */
.media-card img {
  width: 100%;
  height: 220px;           /* compact + consistent */
  object-fit: contain;     /* preserve detail */
  display: block;
  background: transparent;
}

/* Caption */
.cs-media figcaption {
  font-size: 0.95rem;
  line-height: 1.5;
  color: #555;
}

.dark-mode .cs-media figcaption {
  color: #e8d5c4;
}

/* Actions */
.cs-actions {
  margin-top: auto;
  padding-top: 0.25rem;
}

.cs-link {
  font-size: 0.9rem;
  font-weight: 700;
  text-decoration: none;
  color: #6B8E23;
}

.cs-link:hover {
  text-decoration: underline;
}

.dark-mode .cs-link {
  color: #EBA352;
}

/* Optional: keep big screenshots from blowing up */
@media (max-width: 600px) {
  .media-card img {
    height: 190px;
  }
}

.media-card-link {
  text-decoration: none;
  color: inherit;
}

.media-card-link:hover {
  transform: translateY(-4px);
}

.media-card-link:focus {
  outline: 3px solid #6B8E23;
  outline-offset: 4px;
}

.dark-mode .media-card-link:focus {
  outline-color: #EBA352;
}


/* Recruiter CTA line */
.recruiter-cta {
  margin-top: 1.2rem;
  font-size: 0.95rem;
  color: #555;
}

.recruiter-cta a {
  color: #6B8E23;
  font-weight: 700;
  text-decoration: underline;
  text-underline-offset: 4px;
}
.recruiter-cta a:hover {
  opacity: 0.9;
}

/* Recruiter skim card */
.cs-skim {
  margin: 4rem 0;
  padding: 2.5rem;
  border-radius: 24px;
  background: linear-gradient(
    135deg,
    rgba(107, 142, 35, 0.08),
    rgba(107, 142, 35, 0.03)
  );
  border: 1px solid rgba(107, 142, 35, 0.25);
}

.dark-mode .cs-skim {
  background: linear-gradient(
    135deg,
    rgba(235, 163, 82, 0.12),
    rgba(235, 163, 82, 0.04)
  );
  border-color: rgba(235, 163, 82, 0.35);
}

.cs-skim-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.cs-skim-head h3 {
  font-size: 1.6rem;
  font-weight: 800;
}

.cs-skim-badge {
  font-size: 0.7rem;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  padding: 0.4rem 0.8rem;
  border-radius: 999px;
  background: rgba(107, 142, 35, 0.15);
  border: 1px solid rgba(107, 142, 35, 0.35);
}

/* List */
.cs-skim-list {
  list-style: none;
  padding: 0;
}

.cs-skim-list li {
  margin-bottom: 0.8rem;
  font-size: 1rem;
  line-height: 1.6;
}

.cs-micro {
  margin-top: 1.5rem;
}

.cs-micro-link {
  font-size: 0.95rem;
  font-weight: 600;
  text-decoration: none;
  color: #6B8E23;
}

.cs-micro-link:hover {
  text-decoration: underline;
}

.recruiter-toggle-link {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  color: #6B8E23;
  font-weight: 700;
  text-decoration: underline;
  text-underline-offset: 4px;
  cursor: pointer;
  font: inherit;
}

.cs-micro-link {
  background: none;
  border: none;
  padding: 0;
  color: #6B8E23;
  font-weight: 700;
  text-decoration: underline;
  text-underline-offset: 4px;
  cursor: pointer;
  font: inherit;
}

/* optional: improve anchor scroll with fixed nav */
#recruiter-summary,
#full-case-study {
  scroll-margin-top: 120px;
}
